



























import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/sensorManage/api";

@Component({})
export default class ProviderDetail extends Vue {
  @Prop({ type: String, default: "修改传感器供应商" })
  dialogTitle?: string;
  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;
  @Prop({ type: Object })
  ProviderForm!: object;
  @Prop({ type: Boolean, default: false })
  disabled!: boolean;

  @Watch("dialogVisible")
  visibleChange(dialogVisible: boolean) {
    this.addDialog = dialogVisible;
    if (dialogVisible) {
      this.$nextTick(() => {
        let ref: any = this.$refs.ProviderForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.ProviderForm));
      });
    }
  }

  // 表单规则
  rules: any = {
    name: { required: true, message: "请输入供应商名称", trigger: "change" },
    code: { required: true, message: "请输入供应商编码", trigger: "change" }
  };
  addForm: any = {};

  addDialog: boolean = false;
  loading: boolean = false;

  closeDialog() {
    this.addDialog = false;
    this.$emit("close");
  }

  saveSensorProvideribute() {
    let rest: any = this.$refs.ProviderForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.loading = true;
        let url = this.addForm.id ? api.updateProvider : api.addProvider;
        this.$http
          .post(url, this.addForm)
          .then((response: any) => {
            this.loading = false;
            if (response.data.status) {
              this.$message.success(response.data.message);
              this.addDialog = false;
              this.$emit("submit");
            } else {
              this.$message.error(response.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }
}
