

































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/sensorManage/api";
import ProviderDetail from "./components/ProviderDetail.vue";

@Component({
  components: {
    ProviderDetail
  }
})
export default class SensorProviderList extends Vue {
  searchDTO = {
    total: 0,
    pageNum: 1,
    pageSize: 10,
    query: {
      code: "",
      name: ""
    }
  };
  ProviderDialogTitle: string = "";
  ProviderDialogVisible: boolean = false;
  loading: boolean = false;
  ProviderList: Array<any> = [];
  ProviderData: any = {};

  handleAdd() {
    this.ProviderDialogTitle = "新增传感器供应商";
    this.ProviderDialogVisible = true;
    this.ProviderData = {};
  }

  handleEdit(data: any) {
    this.ProviderDialogTitle = "修改传感器供应商";
    this.ProviderDialogVisible = true;
    this.ProviderData = JSON.parse(JSON.stringify(data));
  }

  handleDelete(id: any) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(`${api.deleteProvider}${id}`).then((response: any) => {
        if (response.data.status) {
          this.$message.success(response.data.message);
          this.getProviderList();
        } else {
          this.$message.error(response.data.message);
        }
      });
    });
  }

  pageCurrentChange(current: number) {
    this.searchDTO.pageNum = current;
    this.getProviderList();
  }

  pageSizeChange(size: number) {
    this.searchDTO.pageSize = size;
    this.getProviderList();
  }

  getSearchList() {
    this.searchDTO.pageNum = 1;
    this.getProviderList();
  }

  getProviderList() {
    this.$http.post(api.pagingProvider, this.searchDTO).then((res: any) => {
      this.loading = false;
      if (res.status) {
        if (res.data.status) {
          this.ProviderList = res.data.data.records;
          this.searchDTO.total = res.data.data.total;
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  submitProviderForm() {
    this.getProviderList();
    this.closeDialog();
  }

  closeDialog() {
    this.ProviderDialogVisible = false;
  }

  mounted() {
    this.getProviderList();
  }
}
